@import "../../theme.scss";

#content {
  width: 100%;
  height: 100%;

  .register-container {
    height: 100%;
    padding-top: 70px;
    overflow: hidden;
  }

  .register-inner {
    position: relative;
    height: 100%;
    overflow: hidden;
    overflow-y: auto;

    .l-top {
      position: absolute;
      width: 184px;
      height: 235px;
      top: 0;
      left: 86px;
      background: url('../../images/top.png');
    }

    .l-bottom {
      position: absolute;
      width: 184px;
      height: 235px;
      bottom: 0;
      right: 86px;
      background: url('../../images/bottom.png');
    }
  }

  .register-box {
    position: relative;
    height: 850px;
  }

  .register {
    position: absolute;
    left: 50%;
    top: 50px;
    width: 520px;
    border-radius: 12px;
    border-radius: 12px;
    padding-bottom: 40px;
    overflow: hidden;
    margin-left: -260px;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, 0.2);
    border-radius: 3px;
    z-index: 1;

    .new-reg-guide-login {
      margin: 50px 0 22px 39px;

      h3 {
        font-size: 36px;
        color: #000;
        padding-bottom: 4px;
        margin: 0;
      }

      p {
        font-size: 14px;
        color: #9b9b9b;
        margin: 0;

        span {
          color: $bg-color;
          cursor: pointer;
        }
      }
    }

    .reg-content {
      position: relative;
      overflow: hidden;
      padding-top: 28px;

      .form_item {
        margin-bottom: 40px;

        .col_form_label {
          height: 40px;
          display: flex;
          justify-content: flex-end;
          align-items: center;

          @media screen and (max-width:576px) {
            justify-content: flex-start;
          }
        }

        .form_label {
          font-size: 14px;
          color: #333;
          letter-spacing: 0;
          // width:120px;
          margin-right: 10px;
          font-weight: 200;
          display: block;
        }

        .tooltip {
          width: auto;
        }

        .hasName,
        .pwdReg {
          margin-left: 10px;
        }

        .form_input {
          padding: 11px 10px;
          max-height: 40px;
          background: #fff;
          border: 1px solid #e0e0e0;
          border-radius: 4px;
          font-size: 14px;
          width: 330px;
        }

        .input_focus {
          border-color: $bg-color;
        }

        .verifyCode-input {
          width: 194px;
          margin-right: 9px;
          float: left;
        }

        .button-verifyCodeSend {
          display: inline-block;
          width: 126px;
          min-width: 120;
          cursor: pointer;
          border: 1px solid #e0e0e0;
          height: 40px;
          background: url('../../images/imgload.gif') center no-repeat;
          img{
            width: 100%;
            height: 100%;
          }
        }
      }

      .form_but {
        position: relative;
        margin-top: 30px;
        margin-bottom: 10px;
        // left: 67px;

        .button-reg {
          background: $bg-color;
          width: 100%;
          height: 50px;
          font-size: 16px;
          font-weight: 700;
          cursor: pointer;
          color: #fff;
          border: 0;
        }
      }

      .Agree {
        margin: 0;
        text-align: center;

        span {
          color: red;
          cursor: pointer;
        }
      }
    }
  }

  @media screen and (max-width:576px) {
    .register {
      width: 100%;
      top: 0;
      left: 0;
      margin: 0;
      .new-reg-guide-login{
        margin: 20px 0 10px 20px;
      }

      .reg-content {
        padding: 0 20px;

        .form_item{
          .form_input {
            width: 100%;
          }
          .verifyCode-input{
            width: 65%;
          }
          .button-verifyCodeSend{
            width: 30%;
          }
        }
        .testName{
          position: relative;
          .hasName{
            position: absolute;
            right: 10px;
            top: 10px;
          }
        }
      }
    }
  }

  //用户协议
  .protocol-inner {
    position: fixed;
    width: 740px;
    left: 50%;
    top: 50%;
    background-color: #fff;
    box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, 0.2);
    margin-top: -340px;
    margin-left: -370px;
    z-index: 99;

    .protocol-head {
      display: flex;
      justify-content: space-between;
      padding: 20px;

      h2,
      i {
        height: 40px;
        font-size: 28px;
        color: rgba(0, 0, 0, 1);
        line-height: 40px;
      }

      i {
        cursor: pointer;
      }
    }

    .protocol-content {
      height: 550px;
      padding: 15px 28px;
      overflow: hidden;
      overflow-y: auto;
    }

    @media screen and (max-width:740px) {
      width: 100%;
      height: 100%;
      left: 0;
      margin: 0;
      top: 0;
      .protocol-head{
        h2{
          font-size: 20px;
          height: 20px;
          line-height: 20px;
        }
      }
      .protocol-content{
        height: calc(100% - 70px);
      }
    }
  }

  #Footer {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
  }
}